.icon-scroll {position: relative;  left:15px;  }
.icon-scroll:before {position: absolute;right: -18px;}
.icon-scroll {width: 30px;height: 50px;margin-left: -20px;bottom: -100px;margin-top: -35px;box-shadow: inset 0 0 0 2px #007bff;border-radius: 25px;-webkit-transition: right .5s;transition: right .5s;}
.icon-scroll:before {content: '';width: 6px;height: 6px;background: #007bff;margin-left: -4px;top: 8px;border-radius: 4px;-webkit-animation-duration: 1.5s;animation-duration: 1.5s;-webkit-animation-iteration-count: infinite;animation-iteration-count: infinite;-webkit-animation-name: scroll;animation-name: scroll;left: 50%;}
.icon-scroll:after {content: "";position: absolute;z-index: -1; width: 58px;height: 80px;border-radius: 25px;-webkit-transform: translate(-10px, -10px);transform: translate(-10px, -10px);-webkit-transition: all .3s;transition: all .3s;}
