}
.page-transition-animation .notification-enter{visibility:hidden;-webkit-transform:translate3d(100%, 0, 0) scale(0, 0);transform:translate3d(100%, 0, 0) scale(0, 0)}.page-transition-animation .notification-enter.notification-enter-active{visibility:visible;-webkit-transform:translate3d(0, 0, 0) scale(1, 1);transform:translate3d(0, 0, 0) scale(1, 1);-webkit-transition:all 0.8s;transition:all 0.8s}.page-transition-animation .notification-leave{visibility:visible;-webkit-transform:translate3d(0, 0, 0) scale(1, 1);transform:translate3d(0, 0, 0) scale(1, 1)}.page-transition-animation .notification-leave.notification-leave-active{visibility:hidden;-webkit-transform:translate3d(100%, 0, 0) scale(0, 0);transform:translate3d(100%, 0, 0) scale(0, 0);-webkit-transition:all 0.5s;transition:all 0.5s}


  
