.circle {
    position: relative;
    background-image: url(/static/media/map_icon_std.56405176.svg);
    color: #fff;
    height: 64px;
    position: relative;
    margin-top: -45px;
    margin-left: -25px;
    width: 50px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    background-repeat: no-repeat;
    border: 1px solid transparent;
}
.circle p{
  line-height: 15px;
  margin-bottom: 5px;
  font-size: 14px;
}
.circle:hover {
    cursor: pointer;
    -webkit-transition: all 0.1s linear;
    transition: all 0.1s linear;
    -webkit-transform: scale(0.8) translate(0px, -10px);
            transform: scale(0.8) translate(0px, -10px);
}
.circle-detail {
    visibility: hidden;
    position: absolute;
    background-color: #0078d4;
    -webkit-transform: translateY(19px);
            transform: translateY(19px);
    top: 79%;
    z-index: 999999;
    width: 179px;
    border-radius: 5px;
    left: -65px;
    box-shadow: 2px 2px 12px #00000080;
    cursor: pointer;
    padding: 5px;
    opacity: 0;
    -webkit-transition: all 0.4s linear;
    transition: all 0.4s linear;
    font-size: 13px;
}
.circle-detail::after {
    content: " ";
    position: absolute;
    bottom: 100%;  /* At the top of the tooltip */
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #0078d4 transparent;
}
.circle-subdetail {
    display: none;
    opacity: 0;
    -webkit-transition: all 1s linear;
    transition: all 1s linear;
}
.circle:hover .circle-detail, .circle-detail:hover {
    visibility: visible;
    opacity: 1;
}

.circle.active {
    -webkit-transform: scale(0.8) translate(0px, -10px);
            transform: scale(0.8) translate(0px, -10px);
}
.circle.active .circle-detail, .circle.active .circle-subdetail {
    display: block;
    opacity: 1;
    visibility: visible;
}
